<template>
  <div>
    <mrc_ess_table v-if="$route.name=='systemParameter'"
                   :headers='headers'
                   pageName='addSystemParameter'
                   :paginationOptions='options'
                    :hideAddBtn="!hasBtn('add-system-configuration')"
                   @turnPage='getlist'
                   :isLoading='loading'
                   :slotKey="['param_name','status']"
                   :items='desserts'>
      <template #item.param_name='{item}'>
        {{INT(item.param_name)}}
      </template>

      <!-- 状态 -->
      <template v-slot:item.status='{item}'>
        <v-btn rounded
               color="#13a671"
               dark
               x-small
               v-if="item.status==0">{{$t('label_normal')}}</v-btn>
        <v-btn rounded
               color="#999999"
               dark
               x-small
               v-if="item.status==1">{{$t('label_time_out')}}</v-btn>
      </template>

      <!-- 操作 -->
      <template v-slot:item.actions='{item}'>
        <menu-list :list='menulist'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon v-btnAuth="['view-system-configuration','edit-system-configuration','delete-system-configuration']">mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
    </mrc_ess_table>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import {
  getSystemParameterList, detailSystemParameter
  , addSystemParameter, updateSystemParameter, deleteSystemParameter
} from '@/service/api/systemParameter.js'
export default {
  components: {

  },
  props: {},
  data () {
    return {
      disabled: true,
      list: {},
      options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
      },
      loading: false,
      desserts: [],
    };
  },
  computed: {
    headers () {
      return [
        { text: this.$t('label_The_parameter_name'), align: 'start', value: 'param_name', sortable: false },
        { text: this.$t('label_Parameters_of_the_key_name'), value: 'param_key', sortable: false },
        { text: this.$t('label_value'), value: 'param_key_value', sortable: false, },
        { text: this.$t('label_type'), value: 'business_type', sortable: false, },
        { text: '', value: 'actions', sortable: false, align: 'end', },
      ]
    },
    menulist () {
      return [
        { name: this.$t('label_View'), show: this.hasBtn('view-system-configuration') },
        { name: this.$t('label_Edit'), show: this.hasBtn('edit-system-configuration') },
        { name: this.$t('label_delete'), show: this.hasBtn('delete-system-configuration') },
      ]
    }
  },
  created () { },
  watch: {
    $route (v) {
      if (v.path == '/systemParameter') {
        this.getlist()
      }
    }
  },
  async mounted () {
    await this.getlist()
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  methods: {
    async getlist (obj) {
      this.loading = true
      if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const res = await getSystemParameterList({
        pindex: this.options.cpage, psize: this.options.pagesize
      })
      if (res.code == 200) {
        this.desserts = res.data
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
      }
      this.loading = false
    },
    godetail (item) {
      this.$store.commit('set_iconstatus', true)  //行内进图标显示
      this.$store.commit('set_subPage_name', item.param_name)
      this.$store.commit('set_uuid', item.uuid)  //
      this.$router.push({
        name: 'detailSystemParameter'
      })
    },
    handleClick ({ index }, item) {
      switch (index) {
        case 0:
          this.godetail(item)
          break
        case 1:
          this.$store.commit('set_subPage_name', item.param_name)
          this.$store.commit('set_iconstatus', false)  //行内进图标显示 
          this.$store.commit('set_uuid', item.uuid)  //
          this.$router.push({
            name: 'detailSystemParameter'
          })
          break
        case 2:
          this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') }).then(res => {
            deleteSystemParameter({ uuid: item.uuid }).then(res => {
              if (res.code === '200') {
                this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
                this.getlist()
              }
            })
          })
          break
      }
    },
    addemailtemplate () {
      this.$store.commit('set_subPage_name', '')
      this.$store.commit('set_uuid', '')
      this.$store.commit('set_iconstatus', '')  //行内进图标显示
      this.$router.push({
        name: 'addSystemParameter'
      })
    }
  },
};
</script>

<style scoped>
.cardtitle {
  position: relative;
}
.emailbtn {
  position: absolute;
  right: 70px;
  top: -50px;
}
</style>
